.list {
	position: absolute;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	background-color: #ffffff;
	display: flex;
	flex-direction: column;
	width: 350px;
	padding: 10px;
	top: 54%;
}

.list > li {
	display: flex;
	flex-direction: column;
	padding: 10px;
}

.list > li:hover {
	background-color: #f0f0f0;
}
